import {
  Component,
  Directive,
  InjectionToken,
  Inject,
  EventEmitter,
  Output,
  OnDestroy,
  OnInit,
  Optional,
} from '@angular/core';
import { Router } from '@angular/router';
import { AvailableFeature, BbTemplate, DeprecationsService, FeaturesService } from '@backbase/foundation-ang/core';
import { Observable, of, BehaviorSubject, Subject } from 'rxjs';
import { switchMap, shareReplay, tap, map, take, takeUntil } from 'rxjs/operators';

import { LogoutService, LOGOUT, PageConfig, PAGE_CONFIG } from '@backbase/foundation-ang/web-sdk';
import { SelectContextService, Data, ServiceAgreement, RequestParams } from '../services/select-context.service';
import {
  SelectContextWidgetPropertiesService,
  WidgetProperties,
} from '../services/select-context-widget-properties.service';
import {
  SELECT_CONTEXT_URL_PROCESSOR_SERVICE_TOKEN,
  SelectContextUrlProcessorService,
  RedirectProtocol,
} from '../services/default-select-context-redirect.service';

export interface SelectContextContainerCustomizableContext {
  serviceAgreements: Data | null;
  loading: boolean | null;
  error: any | undefined | null;
  saving: boolean | null;
  params: RequestParams | null;
  widgetProperties: WidgetProperties | null;
  currentContext: ServiceAgreement | undefined | null;
}

export const WINDOW = new InjectionToken<string>('Select Context :: WindowToken');

export const SELECT_CONTEXT_INNER_CONTAINER: AvailableFeature = {
  name: 'SELECT_CONTEXT_INNER_CONTAINER',
  description: 'Include a card container in the select context widget template',
};

@Component({
  selector: 'bb-select-context-container',
  templateUrl: './select-context-container.component.html',
})
export class SelectContextContainerComponent implements OnInit, OnDestroy {
  @Output() back = new EventEmitter<void>();
  @Output() loadingChanged = new EventEmitter<boolean>();
  private readonly destroy$ = new Subject<void>();
  hostRef = this;
  serviceAgreements: Observable<Data> = this.service.serviceAgreements.pipe(
    tap((data: Data) => this.selectFirstIfOnlyOne(data)),
  );
  loading: Observable<boolean> = this.service.loading;
  error: Observable<any | undefined> = this.service.error;
  params: Observable<RequestParams> = this.service.params;
  widgetProperties: Observable<WidgetProperties> = this.widgetPropertiesService.properties;
  currentContext: Observable<ServiceAgreement | undefined> = this.widgetProperties.pipe(
    switchMap(({ includeCurrentContext }: WidgetProperties) => {
      if (includeCurrentContext) {
        return this.service.currentContext;
      }
      return of(undefined);
    }),
    shareReplay(1),
  );

  searchValue = '';
  saving = new BehaviorSubject(false);
  savingError = false;

  constructor(
    private readonly service: SelectContextService,
    private readonly widgetPropertiesService: SelectContextWidgetPropertiesService,
    private readonly router: Router,
    @Inject(WINDOW) private readonly localWindow: any,
    @Inject(SELECT_CONTEXT_URL_PROCESSOR_SERVICE_TOKEN)
    private readonly redirectUrlService: SelectContextUrlProcessorService,
    @Optional() @Inject(PAGE_CONFIG) readonly pageConfig: PageConfig,
    @Inject(LOGOUT) private readonly logoutService: LogoutService,
    private readonly featuresService: FeaturesService,
    private readonly deprecationsService: DeprecationsService,
  ) {
    this.loading.pipe(takeUntil(this.destroy$)).subscribe((event: boolean) => {
      this.loadingChanged.emit(event);
    });
  }

  get hasContainer() {
    return this.featuresService.isEnabled(SELECT_CONTEXT_INNER_CONTAINER);
  }

  search(text: string) {
    this.searchValue = text;
    this.service.search(text);
  }

  loadMore() {
    this.service.loadMore();
  }

  select(serviceAgreement: ServiceAgreement) {
    this.savingError = false;
    this.saving.next(true);
    this.loadingChanged.emit(true);
    this.service
      .select(serviceAgreement)
      .pipe(
        switchMap(() => this.widgetProperties),
        map(({ selectContextRedirectPage }: WidgetProperties): string => selectContextRedirectPage),
        take(1),
      )
      .subscribe(
        (selectContextRedirectPage: string) =>
          this.afterSelectContextSuccess(serviceAgreement, selectContextRedirectPage),
        () => {
          /* error */
          this.savingError = true;
          this.saving.next(false);
        },
      );
  }

  goBack() {
    this.logoutService
      .logout()
      .catch(() => {})
      .then(() => {
        this.back.emit();
      });
  }

  private selectFirstIfOnlyOne(result: Data) {
    if (result.totalCount === 1 && result.params.from === 0 && !result.params.query) {
      this.select(result.items[0]);
    }
  }

  private redirectToUrl(serviceAgreement: ServiceAgreement, url: string, spaMode: RedirectProtocol) {
    this.redirectUrlService
      .processUrl(serviceAgreement, url, spaMode)
      .pipe(
        tap(processedUrl => {
          const bbWindow = this.localWindow;
          const currentUrl = bbWindow.location.pathname;
          const [newUrl] = processedUrl.replace(bbWindow.location.origin, '').split('#');
          bbWindow.location.href = processedUrl;
          if (currentUrl === newUrl) {
            bbWindow.location.reload(true);
          }
        }),
        take(1),
      )
      .subscribe();
  }

  private afterSelectContextSuccess(serviceAgreement: ServiceAgreement, urlModel = '') {
    const spaMode = urlModel.substring(0, 4);
    const urlToNavigate = urlModel.substring(4);
    const bbWindow = this.localWindow;

    switch (spaMode) {
      case 'spa:':
        if (bbWindow.reloadPage) {
          bbWindow.reloadPage().then(() => {
            this.redirectUrlService
              .processUrl(serviceAgreement, urlToNavigate, spaMode)
              .pipe(
                tap(url => this.router.navigateByUrl(url)),
                take(1),
              )
              .subscribe();
          });
        } else {
          throw new Error('window.reloadPage() not available. SPA navigation is not supported');
        }
        break;

      case 'mpa:':
        const gatewayRoot = this.pageConfig.linkRoot || '';
        const portalName = this.pageConfig.portalName || '';
        const mpaUrl = `${gatewayRoot}${portalName ? '/' + portalName : ''}/${urlToNavigate}`.replace(/\/{2,}/g, '/');
        this.redirectToUrl(serviceAgreement, mpaUrl, spaMode);
        break;

      case 'abs:':
        const linkRoot = this.pageConfig.linkRoot || '';
        const absUrl = `${linkRoot}/${urlToNavigate}`.replace(/\/{2,}/g, '/');
        this.redirectToUrl(serviceAgreement, absUrl, spaMode);
        break;
    }
  }

  ngOnInit() {
    if (!this.hasContainer) {
      this.deprecationsService.logDeprecatedFeature(
        'Select context widget will include card container in its template in the next major release. Please enable `SELECT_CONTEXT_INNER_CONTAINER` and adjust your page to support this change.',
      );
    }
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}

@Directive({
  selector: '[bbSelectContextContainerCustomizable]',
})
export class SelectContextContainerCustomizableDirective extends BbTemplate<
  SelectContextContainerComponent,
  SelectContextContainerCustomizableContext
> {}

@Directive({
  selector: '[bbSelectContextContainerLoadingCustomizable]',
})
export class SelectContextContainerLoadingCustomizableDirective extends BbTemplate<
  SelectContextContainerComponent,
  undefined
> {}

@Directive({
  selector: '[bbSelectContextContainerSavingCustomizable]',
})
export class SelectContextContainerSavingCustomizableDirective extends BbTemplate<
  SelectContextContainerComponent,
  undefined
> {}
