import { Injectable, InjectionToken } from '@angular/core';
import { Observable, of } from 'rxjs';
import { ServiceAgreement } from './select-context.service';

export const SELECT_CONTEXT_URL_PROCESSOR_SERVICE_TOKEN = new InjectionToken<SelectContextUrlProcessorService>(
  'Select Context :: SELECT_CONTEXT_URL_PROCESSOR_SERVICE_TOKEN',
);

export type RedirectProtocol = 'mpa:' | 'spa:' | 'abs:';

export interface SelectContextUrlProcessorService {
  processUrl(serviceAgreement: ServiceAgreement, navigateToUrl: string, spaMode: RedirectProtocol): Observable<string>;
}

@Injectable()
export class DefaultSelectContextRedirectService implements SelectContextUrlProcessorService {
  /* eslint-disable @typescript-eslint/no-unused-vars */
  //@ts-ignore
  processUrl(serviceAgreement: ServiceAgreement, urlToNavigate: string, spaMode: RedirectProtocol): Observable<string> {
    return of(urlToNavigate);
  }
}
