import { Injectable } from '@angular/core';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import {
  UserContextHttpService,
  ServiceAgreementHttpService,
  Serviceagreementpartialitem as ServiceAgreement,
  GetUserContextServiceAgreementsRequestParams as RequestParams,
} from '@backbase/data-ang/accesscontrol';
import { Observable, BehaviorSubject, of, combineLatest } from 'rxjs';
import { switchMap, catchError, map, tap, scan } from 'rxjs/operators';

import { parseError, SelectContextError } from '../select-context-error';
import { SelectContextWidgetPropertiesService, WidgetProperties } from './select-context-widget-properties.service';

export {
  Serviceagreementpartialitem as ServiceAgreement,
  GetUserContextServiceAgreementsRequestParams as RequestParams,
} from '@backbase/data-ang/accesscontrol';

const initialParams: RequestParams = {
  from: 0,
  size: 7,
};

export interface Data {
  items: ServiceAgreement[];
  totalCount: number;
  params: RequestParams;
}

@Injectable()
export class SelectContextService {
  constructor(
    private readonly userContextService: UserContextHttpService,
    private readonly serviceAgreementService: ServiceAgreementHttpService,
    private readonly widgetPropertiesService: SelectContextWidgetPropertiesService,
  ) {}

  readonly params = new BehaviorSubject<RequestParams>(initialParams);
  readonly loading = new BehaviorSubject<boolean>(true);
  readonly error = new BehaviorSubject<SelectContextError | undefined>(undefined);

  readonly serviceAgreements: Observable<Data> = combineLatest([
    this.params,
    this.widgetPropertiesService.properties,
  ]).pipe(
    map(([params, { pageSize }]: [RequestParams, WidgetProperties]): RequestParams => ({ ...params, size: pageSize })),
    tap(() => this.loading.next(true)),
    switchMap((params: RequestParams) => this.loadServiceAgreements(params)),
    scan((acc: Data, curr: Data) => this.mergeResults(acc, curr)),
    catchError((error: SelectContextError) => {
      this.error.next(error);
      return of({ items: [], totalCount: 0, params: this.params.value });
    }),
    tap(() => this.loading.next(false)),
  );

  readonly currentContext: Observable<ServiceAgreement | undefined> = this.serviceAgreementService
    .getServiceAgreementContext()
    .pipe(catchError(() => of(undefined)));

  private loadServiceAgreements(params: RequestParams): Observable<Data> {
    return this.userContextService.getUserContextServiceAgreements(params, 'response').pipe(
      map((response: HttpResponse<ServiceAgreement[]>) => {
        const items = response.body || [];
        return {
          items,
          totalCount: parseInt(response.headers.get('x-total-count') || '0', 10) || items.length,
          params,
        };
      }),
      catchError((error: HttpErrorResponse) => {
        throw parseError(error);
      }),
    );
  }

  private mergeResults(acc: Data, curr: Data): Data {
    return {
      totalCount: curr.totalCount || curr.items.length,
      items: curr.params.from === 0 ? curr.items : [...acc.items, ...curr.items],
      params: curr.params,
    };
  }

  search(query: string) {
    this.params.next({
      from: 0,
      size: this.params.value.size,
      ...(query ? { query } : {}),
    });
  }

  loadMore() {
    this.params.next({
      ...this.params.value,
      from: (this.params.value.from || 0) + 1,
    });
  }

  select(serviceAgreement: ServiceAgreement) {
    return this.userContextService.postUserContext({
      userContextPOST: {
        serviceAgreementId: serviceAgreement.id,
      },
    });
  }
}
