import { HttpErrorResponse } from '@angular/common/http';
import { parseError as commonParseError, WidgetError } from '@backbase/accessgroup-common-ang';

// Empty interface because package:apps with tsconfig.packaged.json.
// It also fails when using type since it tries to import WidgetError in other files
// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface SelectContextError extends WidgetError {}

export const parseError = (error: HttpErrorResponse): SelectContextError => commonParseError('SelectContext', error);
