import { Component, Input, Output, EventEmitter, Directive } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';

import { ServiceAgreement } from '../services/select-context.service';

export interface ServiceAgreementListCustomizableContext {
  serviceAgreements: ServiceAgreement[] | null;
  currentContext: ServiceAgreement | undefined | null;
  loading: boolean | null;
  searchValue: string | null;
  totalCount: number | null;
  from: number | null;
}

@Component({
  selector: 'bb-service-agreement-list',
  templateUrl: './service-agreement-list.component.html',
})
export class ServiceAgreementListComponent {
  hostRef = this;
  @Input() serviceAgreements: ServiceAgreement[] = [];
  @Input() currentContext: ServiceAgreement | undefined;
  @Input() loading = false;
  @Input() searchValue = '';
  @Input() totalCount = 0;
  @Input() from = 0;

  @Output() loadMore = new EventEmitter<void>();
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() select = new EventEmitter<ServiceAgreement>();

  onLoadMore() {
    this.loadMore.emit();
  }

  onSelect(serviceAgreement: ServiceAgreement) {
    this.select.emit(serviceAgreement);
  }
}

@Directive({
  selector: '[bbSelectContextServiceAgreementListCustomizable]',
})
export class SelectContextServiceAgreementListCustomizableDirective extends BbTemplate<
  ServiceAgreementListComponent,
  ServiceAgreementListCustomizableContext
> {}
